<template>
  <div id="app">
    <h1>在线翻译</h1>
    <h5 class="text-muted">简单 / 暴力 / 好用</h5>
    <translateForm v-on:formS="TranslateText"></translateForm>
    <translate-output v-text="translatedText"></translate-output>
  </div>
</template>

<script>
import TranslateForm from './components/TranslateForm'
import TranslateOutput from './components/Output'

export default {
  name: 'App',
  data:function(){
    return{
      translatedText:""
    }
  },
  components: {
    TranslateForm,
    TranslateOutput
  },
  methods:{
    TranslateText:function(text,language){
      // alert(text);
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20181110T032438Z.ed964b94d8f1a2d3.38b6380b4fdb0245b9f9a7b8141ca6df8aa4f6fe&lang='+language+'&text='+text).then((response)=>{
        // console.log(response.body.text[0]);
        this.translatedText = response.body.text[0];
      })
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



